import React from 'react';
import { Container, Button } from '@material-ui/core';
import { useFoodStateValue } from '../context/FoodStateContext';
import { useFoodStateHelpers } from '../hooks/useFoodStateHelpers';
import { RecipeCard, LottieAnimation } from '../compontents';
import { Chips } from '../compontents/Chips';
import { Chiper } from '../compontents/Chiper';
import { SEASONING_LIST } from '../constants/mock-seasoning';

import { makeStyles } from '@material-ui/core/styles';
import { MenuItemsListModal } from '../compontents/MenuItemsListModal';

const useStyles = makeStyles((theme) => ({
  modalStyle: {
    maxWidth: '75%',
    minWidth: '75%',
    minHeight: '26rem',
  },
  chipsContainer: {
    display: 'flex',
    justifyContent: 'start',
    flexWrap: 'wrap',
    '& > *': {
      margin: theme.spacing(0.5),
    },
  },
}));

export const RecipeItemsPage = () => {
  const { foodState, setFoodState } = useFoodStateValue();
  const { isFoodStateEmpty } = useFoodStateHelpers();
  const classes = useStyles();

  const renderFoodItems = () => {
    const foodStateItems = foodState.map(
      ({
        foodId,
        title,
        category,
        grammage,
        grammageInfo,
        nutrition,
        imgSrc,
        variants,
      }) => {
        return (
          <RecipeCard
            foodId={foodId}
            key={title}
            setFoodState={setFoodState}
            title={title}
            category={category}
            grammage={grammage}
            grammageInfo={grammageInfo}
            nutrition={nutrition}
            imgSrc={imgSrc}
            variants={variants}
          />
        );
      }
    );

    return foodStateItems;
  };

  //!write own style on h3
  return (
    <Container className='recipe'>
      {isFoodStateEmpty ? (
        <div className='recipe__default'>
          <LottieAnimation url='./images/animations/empty-bag.json' />
          <h3>PRZDNO</h3>
        </div>
      ) : (
        <>
          <div className='recipe__header'>
            <MenuItemsListModal modalStyle={classes.modalStyle}>
              {
                //? Button
                <Button style={{ marginBottom: '10px' }} color='primary'>
                  + Koen a Ingredience
                </Button>
              }
              {
                //? Content
                <div className='seasoning-modal'>
                  <div className='seasoning-modal__body'>
                    <div className='seasoning-modal__section'>
                      <h3>Koen</h3>
                      <div
                        className={`seasoning-modal__chips ${classes.chipsContainer}`}
                      >
                        {SEASONING_LIST.map((seasoning) => (
                          <Chiper seasoning={seasoning} />
                        ))}
                      </div>
                    </div>
                    <div className='seasoning-modal__section'>
                      <h3>Ingredience</h3>
                      <div className='seasoning-modal__chips'>
                        {/* <Chiper label='Sl' /> */}
                      </div>
                    </div>
                    <div className='seasoning-modal__section'>
                      <h3>Omky</h3>
                      <div className='seasoning-modal__chips'>
                        {/* <Chiper label='Sl' /> */}
                      </div>
                    </div>
                  </div>
                </div>
              }
            </MenuItemsListModal>

            <Chips />
          </div>
          <div className='recipe__body'>
            <div className='recipe__content'>{renderFoodItems()}</div>
          </div>
        </>
      )}
    </Container>
  );
};
